@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.google.com/icons?selected=Material%20Icons%20Outlined%3Asearch);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0px;
    margin: 0px;
}
:root{
    --blue:#2195EB;
    --mont:'Montserrat';
}
#bg:before{
    content: ' ';
    width: 100%;
    height: 23vh;
    background:url(/project1_asper/static/media/blackmamba.4bc7d6f3.png) repeat-x center center/cover;
    position: absolute;
    z-index: -1;
}
.bg{
    width: 45%;
    margin: auto;
    height: 23vh;
    position: relative;
}
.title{
    color: white;
    padding:30px 25px;
    font-family:'Montserrat';
    font-family:var(--mont);
}
h1{
    font-size: 40px;
}
.title span{
    color:#2195EB;
    color:var(--blue);
}
nav{
    background-color: #2195EB;
    background-color: var(--blue);
    position: absolute;
    bottom: 0px;
    width: 100%;
}
nav ul li{
    display: inline-block;
    padding: 25px;
}
nav ul li a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-family:'Montserrat';
    font-family:var(--mont);
    font-weight: bold;
    padding: 25px 0px;
}
nav li:hover{
    background-color: #1c6faf;
    ;
}



*{
    padding: 0px;
    margin: 0px;
}
:root{
    --blue:#2195EB;
    --mont:'Montserrat';
}
.main::before{
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 77vh;
    background: url(/project1_asper/static/media/furley_bg.d37cf31d.png) repeat center center/cover;
    -ms-interpolation-mode: nearest-neighbor;
        image-rendering: -moz-crisp-edges;
        image-rendering: pixelated;

}
.center{
    width: 45%;
    background-color: #fff;
    height: 77vh;
    margin: auto;
    font-family:'Montserrat';
    font-family:var(--mont);
    line-height: 1.2rem;
    
}
.cent{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row;
            flex-flow: row;
}
#left{
    width: 70%;
    padding: 30px;
}
.center h2,.center p,.center h3,.center ul,#right li a,#right h3,#right h5{
    padding-top: 20px;
}
.list li{
    list-style-type: none;
}
.list li::before {
    content: "\25E6";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: #2195EB;
    color: var(--blue); /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: inline-block; /* Needed to add space between the bullet and the text */
    width: 2em;
  }
  #left p ,.center li{
    font-size: 12px;
  }
  #left h2{
      color: #2195EB;
      color: var(--blue);
  }
  .center a{
      color: rgb(253, 44, 236);
  }
 #right{
     padding: 30px;
 }
  #right h5{
      color: #2195EB;
      color: var(--blue);
  }
 #right p{
     font-size: 12px;
 }
 #right li{
     list-style-type:none;
 }
 #right ul li::before {
    content: "\1F4CE";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
    color: #2195EB;
    color: var(--blue); /* Change the color */
    font-weight: bold; /* If you want it to be bold */
    display: inline-block; /* Needed to add space between the bullet and the text */
    width: 2em;
  }
  input{
      margin-top: 50px;
      margin-right: 10px;
      height: 1.5rem;
  }
 footer{
     text-transform: uppercase;
     background-color: #2195EB;
     background-color: var(--blue);
     width: 45%;
     margin: auto;
     padding: 20px 0px;
     position: absolute;
     bottom: 0px;
     text-align: center;
     color: white;
     font-weight: bold;
 }
